@import "../assets/style/variable.scss";

.navigation {
  position: relative;
  width: 45px;
  .navBtn {
    position: sticky;
    top: 0;
    right: 0;
    width: 150px;

    a {
      transform: rotate(270deg);
      background: url(../assets/images/tab-bg-default.svg) no-repeat 50% / cover;
      width: 150px;
      height: 45px;
      line-height: 45px;
      color: #fff;
      text-align: center;
      background-color: orange;
    }
  }
}
